.aboutArea {
    .aboutGroup {
        display: flex; flex-wrap: wrap; gap: 40px 20px;
        .aboutUnit {
            flex: 0 0 calc(50% - 10px); border: 1px solid #ccc; border-radius: 15px; padding: 35px 25px; position: relative;
            &:last-child {flex: 0 0 100%;}
            .abtTit {font-size: 1.35rem; padding: 5px 20px; background: var(--Base-bg); position: absolute; top: -20px; left: 0;}
            .dtlItem {
                margin-bottom: 10px; font-size: 1.05rem; font-weight: 500; display: flex; justify-content: space-between; align-items: center;
                &:last-child {margin-bottom: 0;}
                .years {font-size: 1rem; color: #666; font-weight: 400;}
            }
            .sklGroup {
                display: flex; flex-wrap: wrap; gap: 20px 30px; justify-content: space-between;
                .skill {
                    font-size: 1.05rem;
                    b {font-weight: 600;}
                }
            }
        }
    }
}
.contactArea {
    height: 100%; display: flex; align-items: center; justify-content: center;
    .cttGroup {
        display: flex; flex-wrap: wrap; gap: 0 40px; justify-content: center; align-items: center;
        .cttItem {
            margin-bottom: 40px; 
            &:last-child {margin-bottom: 0;}
            .titleImg {
                width: 260px; height: 260px; border-radius: 50%; border: 1px solid #ccc; overflow: hidden;
                img {width: 120%; height: 120%; object-fit: cover; transform: translate(-10%, -10%);}
            }
            .ctt {
                margin-bottom: 20px; display: flex; align-items: center; justify-content: space-between;
                &:last-child {margin-bottom: 0;}
                dt {
                    flex: 0 0 23%; line-height: 1.3; text-align: center;
                    b {width: 100%; font-size: 1.15rem; font-weight: 600; display: inline-block;}
                    i {width: 100%; font-style: normal; font-weight: 300; color: #666; letter-spacing: 0.15rem; display: inline-block;}
                }
                dd {flex: 0 0 77%; text-align: right; font-size: 1.25rem;}
            }
        }
    }
    
}